import { useState } from 'react';

import SearchForm from '../../components/SearchForm/SearchForm';
import MovieList from '../../components/MovieList/MovieList';
import MovieDetail from '../../components/MovieList/MovieDetail';

const Search = () => {
  // State searchTerm nhn gi tr t submit SearchForm, sau  truyn vo MovieList
  // State selectedMovie thng bo m ng v cung cp thng tin cho MovieDetail
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedMovie, setSelectedMovie] = useState({
    detail: {},
    target: null,
  });
  // Click event, ngi dng click vo cng i tng Movie th ng MovieDetail, xa .__active
  // Ngi dng click vo Movie khc th hin th MovieDetail mi, xa .__active  Movie c v thm .__active  Movie mi
  const resetSelectedMovie = () => {
    setSelectedMovie({
      detail: {},
      target: null,
    });
  };

  const setNewSelectedMovie = (movie, target) => {
    setSelectedMovie({
      detail: movie,
      target: target,
    });
  }
  const handleClick = (movie, _, e) => {
    if (e.target === selectedMovie.target) {
      selectedMovie.target.classList.remove('__active');
      resetSelectedMovie();
    } else {
      selectedMovie.target?.classList.remove('__active');
      e.target.classList.add('__active');
      setNewSelectedMovie(movie, e.target);
    }
  };
  const submitSearchTerm = (term) => {
    resetSelectedMovie();
    setSearchTerm(term);
  };
  // SearchForm  setSearchTerm
  // MovieList hin th Movie da trn Search Term
  // MovieDetail c hin th khi selectedMovie.target != null
  return (
    <div className="app py-5">
      <SearchForm submitSearchTerm={submitSearchTerm} />
      <div className="container-xxl row m-0">
        <div className={selectedMovie.target && 'col-7'}>
          <MovieList
            selectedCategory={['Search']}
            searchTerm={searchTerm}
            handleClick={handleClick}
          />
        </div>
        {selectedMovie.target && (
          <div className="col">
            <MovieDetail
              key={selectedMovie.detail.id}
              {...selectedMovie.detail}
            />
          </div>
        )}
      </div>
    </div>
  );
};

export default Search;
