import { useState, useCallback, Fragment } from 'react';

import Banner from '../../components/Banner/Banner';
import MovieList from '../../components/MovieList/MovieList';
import MovieDetail from '../../components/MovieList/MovieDetail';

function Browse() {
  // Danh sch cc categories  hin th list movies
  const categories = {
    NetflixOriginals: '',
    Trending: 'Xu hng',
    TopRated: 'Xp hng cao',
    ActionMovies: 'Hnh ng',
    ComedyMovies: 'Hi',
    HorrorMovies: 'Kinh d',
    RomanceMovies: 'Lng mng',
    Documentaries: 'Ti Liu',
  };
  // selectedMovie  m ng v cung cp d liu cho MovieDetail
  // setSelectedMovies c truyn vo MovieList  x l click event ca ngi dng
  const [selectedMovie, setSelectedMovie] = useState({
    detail: {},
    listId: null,
    target: null,
  });

  // Click event, ngi dng click vo cng i tng Movie th ng MovieDetail, xa .__active
  // Ngi dng click vo Movie khc th hin th MovieDetail mi, xa .__active  Movie c v thm .__active  Movie mi, nu thuc MovieList khc th ng MovieDetail  v tr MovieList c
  const handleClick = (movie, listId, e) => {
    if (e.target === selectedMovie.target) {
      selectedMovie.target.classList.remove('__active');
      setSelectedMovie({
        detail: {},
        listId: null,
        target: null,
      });
    } else {
      selectedMovie?.target?.classList.remove('__active');
      e.target.classList.add('__active');
      setSelectedMovie({
        detail: movie,
        listId: listId,
        target: e.target,
      });
    }
  };

  return (
    <div className="app">
      <Banner />
      <div className="container-xxl">
        {/* Map cc MovieList */}
        {Object.entries(categories).map((entry, i) => (
          <Fragment key={i}>
            <MovieList
              id={i}
              selectedCategory={entry}
              handleClick={handleClick}
            />
            {/* Kim tra selectedMovie.listId v selectedMovie.detail  hin th MovieDetail  v tr tng ng nu c */}
            {selectedMovie.listId === i && selectedMovie.detail && (
              <MovieDetail
                key={selectedMovie.detail.id}
                {...selectedMovie.detail}
              />
            )}
          </Fragment>
        ))}
      </div>
    </div>
  );
}

export default Browse;
