import { useState, useEffect } from 'react';
import useHttp from '../../hooks/useHttp';

import YouTube from 'react-youtube';

import classes from './MovieDetail.module.css';

const MovieDetail = (props) => {
  const {
    id,
    title,
    name,
    release_date: rDate,
    first_air_date: faDate,
    vote_average: vote,
    overview,
    apiKey,
  } = props;
  const opts = {
    height: '400',
    width: '100%',
    playerVars: {
      autoplay: 0,
    },
  };
  // State media cha video miu t nu c, khng th backdrop_path Img ca Movie
  const [media, setMedia] = useState(null);
  const { isLoading, error, sendRequest } = useHttp();
  const url = `https://api.themoviedb.org/3/movie/${id}/videos?api_key=${apiKey}`;

  useEffect(() => {
    // Fnc getMedia  x l d liu tr v sau khi fetch
    const getMedia = (data) => {
      const results = data.results;
      // properMedia l mng cha cc kt qu c site l 'YouTube' v type l 'Trailer' hoc 'Teaser'
      // Lc kt qu v push vo properMedia
      const properMedia = [];
      results.forEach((result) => {
        if (
          result.site === 'YouTube' &&
          (result.type === 'Trailer' || result.type === 'Teaser')
        ) {
          properMedia.push(result);
        }
      });
      // Nu properMedia khng c property th throwError cho useHttp
      // Sort properMedia, a kt qu c type 'Trailer' ln u
      if (properMedia.length === 0) throw new Error('No properMedia');
      if (properMedia.length > 0) {
        properMedia.sort((a) => {
          if (a.type === 'Trailer') return -1;
          if (a.type === 'Teaser') return 1;
        });
        // Truyn videoId t key ca properMedia  v tr u tin cng opts
        setMedia(<YouTube videoId={properMedia[0].key} opts={opts} />);
      }
    };
    // Gi send request  fetch url v x l data bng getMedia function
    sendRequest(url, getMedia);
  }, []);

  useEffect(() => {
    // Kim tra error state tr v t useHttp, nu true th setMedia bng backdrop img
    if (error) {
      setMedia(
        <div>
          <img
            className="object-fit-cover w-100"
            src={`${props.imgSrc}${props.backdrop_path}`}
          ></img>

        </div>
      );
    }
  }, [error]);
  // Hin th cc data  c x l, category NetFlixOriginal c cc properties khc cc phn cn li cc categories nn cn kim tra s tn ti ca cc key trc
  return (
    <div className={classes['detail-container']}>
      <div className={classes['info']}>
        <h4 className="fw-bold">{title ?? name}</h4>
        <div className="fw-bold">
          <p>
            {rDate ? `Release Date: ${rDate}` : `First Air Date: ${faDate}`}
          </p>
          <p>{`Vote: ${vote}/10`}</p>
        </div>
        <div className="overview">{overview}</div>
      </div>
      {media}
    </div>
  );
};
export default MovieDetail;
