import { useState, useContext, useEffect, useCallback } from 'react';
import useHttp from '../../hooks/useHttp';

import classes from './MovieList.module.css';
import Movie from './Movie';

import RequestCtx from '../../context/request-ctx';

const MovieList = ({ id, selectedCategory, handleClick, searchTerm }) => {
  const [category, vietSub] = selectedCategory;
  const [movies, setMovies] = useState([]);
  const [options, imgSrc, apiKey] = useContext(RequestCtx);
  // Nu selectedCategory l Search th thm searchTerm vo sau Url ly t fetchSearch
  const url = `${options[`fetch${category}`]}${
    category === 'Search' ? searchTerm : ''
  }`;
  const { isLoading, error, sendRequest } = useHttp();

  useEffect(() => {
    // Fetch d liu t url xong setMovies  a vo component
    // nu Category l Search th Url s thay i nn thm Url list denpendencies, ng thi cng l rng State movies mi ln useEffect chy li
    setMovies([]);
    const getMoviesList = (data) => {
      data.results.forEach((movie) => {
        if (movie.poster_path && movie.backdrop_path) {
          setMovies((prevMovies) => [...prevMovies, movie]);
        }
      });
    };
    sendRequest(url, getMoviesList);
  }, [url]);
  // Category l NetflixOriginals hay Search th s render Img da trn poster_path
  // Category l NetflixOriginals th ch render ra 10 movie
  // Truyn handleClick v bind movie + listId
  if (category === 'NetflixOriginals' || category === 'Search') {
    return (
      <>
        <div className={classes['movies-list__original']}>
          {movies.map((movie, i) => {
            if (i <= 9 || category === 'Search') {
              return (
                <Movie
                  key={movie.id + i}
                  imgSrc={imgSrc}
                  movie={movie}
                  display={'poster'}
                  handleClick={handleClick.bind(null, {...movie, apiKey: apiKey, imgSrc: imgSrc}, id && id)}
                />
              );
            }
          })}
        </div>
      </>
    );
  }
  // Cc categories khc th render Img da trn poster_path (mc nh), cng truyn handleClick tng t trn
  return (
    <div className="position-relative">
      <h5 className="text-white">{vietSub}</h5>
      <div className={classes['movies-list']}>
        {movies.map((movie, i) => {
          return (
            <Movie
              key={movie.id + i}
              imgSrc={imgSrc}
              movie={movie}
              handleClick={handleClick.bind(null, {...movie, apiKey: apiKey, imgSrc: imgSrc}, id && id)}
            />
          );
        })}
      </div>
    </div>
  );
};

export default MovieList;
